.slider {
  width: 1070px;
  height: 680px;
  background: #eee center / cover;
  grid-template-rows: 1fr;
  grid-template-columns: 4rem 1fr 4rem;
  display: grid;
  position: absolute;
  top: 0;
  left: 0;
}

.slider-slides-cnt {
  grid-area: 1 / 1 / -1 / -1;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr;
  display: grid;
}

.slider-slide {
  opacity: 0;
  z-index: 0;
  flex-direction: column;
  grid-area: 1 / 1 / 2 / 2;
  justify-content: center;
  align-items: center;
  display: flex;
}

.slider-slide-active {
  opacity: 1;
  z-index: 1;
}

.slider .element-title {
  color: #fff;
  opacity: 0;
  text-transform: uppercase;
  text-align: center;
  margin-top: 0;
  font-family: Roboto Condensed, sans-serif;
  font-size: 6rem;
  font-weight: normal;
  position: relative;
  left: -3rem;
}

.slider .element-text {
  color: #fff;
  opacity: 0;
  color: rgba(255, 255, 255, .6);
  text-align: center;
  max-width: 60%;
  padding-left: 4rem;
  padding-right: 4rem;
  font-family: serif;
  font-size: 1.5rem;
  font-style: italic;
  position: relative;
  left: -3rem;
}

.slider-slide {
  opacity: 0;
  transition: opacity 1s cubic-bezier(.455, .03, .515, .955);
}

.slider-slide-active {
  opacity: 1;
}

.slider .element-title {
  opacity: 0;
  transition: left 2s, opacity 2s;
  left: -3rem;
}

.slider .element-text {
  opacity: 0;
  transition: left 2s 1s, opacity 2s 1s;
  left: -3rem;
}

.slider-slide-active .element-title, .slider-slide-active .element-text {
  opacity: 1;
  left: 0;
}

.slider-nav {
  justify-content: center;
  align-items: center;
  gap: 60px;
  display: flex;
  position: absolute;
  bottom: 2em;
  right: 4em;
}

.slider-button-prev, .slider-button-next {
  z-index: 2;
  cursor: pointer;
  color: #9da4bd;
  letter-spacing: .1em;
  border: 0;
  align-items: center;
  padding-bottom: 8px;
  font-size: 12px;
  font-weight: 600;
  line-height: 1.27273;
  display: flex;
  position: relative;
}

.slider-button-next {
  justify-content: flex-end;
}

.slider-button-prev:focus, .slider-button-next:focus, .slider-button-prev:hover, .slider-button-next:hover {
  color: #fff;
}

.slider-button-prev:before, .slider-button-next:before {
  content: "";
  width: 130%;
  height: 1px;
  background-color: currentColor;
  display: block;
  position: absolute;
  bottom: 0;
}

.slider-pagination {
  z-index: 2;
  width: 100%;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  position: absolute;
  bottom: 1rem;
  left: 0;
}

.slider-pagination-button {
  width: 2rem;
  height: 2rem;
  text-indent: -999px;
  cursor: pointer;
  background: none;
  border: 0;
  position: relative;
  overflow: hidden;
}

.slider-pagination-button:before {
  content: "";
  width: .9rem;
  height: .9rem;
  background: none;
  border-radius: 50%;
  transition: all .5s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: inset 0 0 0 2px rgba(255, 255, 255, .5);
}

.slider-pagination-element-active .slider-pagination-button:before {
  background: #fff;
  transform: translate(-50%, -50%)scale(1.2);
}

.slider-pagination-button:focus {
  outline: none;
}

